<template>
  <p-select-button
    :options="options"
    :option-label="'label'"
    :option-value="'value'"
    :model-value="inputValue"
    @update:model-value="handleChange"
    @blur="handleBlur"
  />
</template>

<style lang="scss">
.p-selectbutton {
  display: flex;
  align-items: center;
  justify-content: stretch;
  margin-bottom: 1rem;

  .p-button {
    width: 100%;
  }
}
</style>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import { useField } from "vee-validate";

export interface UiSelectButtonOption<T> {
  label: string;
  value: T;
}

const UiSelectButton = defineComponent({
  props: {
    name: {
      type: String,
      required: true
    },
    options: {
      type: Array as PropType<UiSelectButtonOption<unknown>[]>,
      required: true
    }
  },
  setup(props) {
    const {
      value: inputValue,
      errorMessage,
      handleBlur,
      handleChange,
      meta
    } = useField(props.name);

    return {
      handleChange,
      handleBlur,
      errorMessage,
      inputValue,
      meta
    };
  }
});

export default UiSelectButton;
</script>
