
import { useField } from "vee-validate";
import { defineComponent, computed } from "vue";

const UiInputText = defineComponent({
  props: {
    name: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: false,
      default: ""
    },
    type: {
      type: String,
      required: false,
      default: "text"
    },
    icon: {
      type: String,
      required: false,
      default: ""
    }
  },
  setup(props) {
    const {
      value: inputValue,
      errorMessage,
      handleBlur,
      handleChange,
      meta
    } = useField(props.name);

    const showError = computed(() => meta.dirty && !meta.valid);

    return {
      handleChange,
      handleBlur,
      errorMessage,
      inputValue,
      meta,
      showError
    };
  }
});

export default UiInputText;
