import { NormalizedScheme } from "@/core/utils/normalize";
import { GoodCategory } from "@/models/good-category.model";
import { User } from "@/models/user.model";
import { MutationTree } from "vuex";
import { RootState } from "./interfaces";

export enum RootMutations {
  SIGN_UP_REQUEST = "signUpRequest",
  SIGN_UP_SUCCESS = "signUpSuccess",
  SIGN_UP_FAILURE = "signUpFailure",
  SIGN_IN_REQUEST = "signInRequest",
  SIGN_IN_SUCCESS = "signInSuccess",
  SIGN_IN_FAILURE = "signInFailure",
  CHECK_INITIAL_USER_STATUS_START = "checkInitialUserStatusStart",
  CHECK_INITIAL_USER_STATUS_COMPLETE = "checkInitialUserStatusComplete",
  GET_CATEGORIES_REQUEST = "getCategoriesRequest",
  GET_CATEGORIES_SUCCESS = "getCategoriesSuccess",
  GET_CATEGORIES_FAILURE = "getCategoriesFailure",
  SIGN_OUT_REQUEST = "signOutRequest",
  SIGN_OUT_SUCCESS = "signOutSuccess",
  SIGN_OUT_FAILURE = "signOutFailure",
  UPDATE_CURRENT_USER = "updateCurrentUser"
}

const rootMutations: MutationTree<RootState> = {
  [RootMutations.SIGN_UP_REQUEST](state) {
    state.isSignUpLoading = true;
  },
  [RootMutations.SIGN_UP_SUCCESS](state, user: User) {
    state.isSignUpLoading = false;
    state.currentUser = user;
  },
  [RootMutations.SIGN_UP_FAILURE](state) {
    state.isSignUpLoading = false;
  },
  [RootMutations.SIGN_IN_REQUEST](state) {
    state.isSignInLoading = true;
  },
  [RootMutations.SIGN_IN_SUCCESS](state, user: User) {
    state.isSignInLoading = false;
    state.currentUser = user;
  },
  [RootMutations.SIGN_IN_FAILURE](state) {
    state.isSignInLoading = false;
  },
  [RootMutations.CHECK_INITIAL_USER_STATUS_START](state) {
    state.isCheckStatusLoading = true;
  },
  [RootMutations.CHECK_INITIAL_USER_STATUS_COMPLETE](state, user: User | null) {
    state.isCheckStatusLoading = false;
    state.currentUser = user;
  },
  [RootMutations.GET_CATEGORIES_REQUEST](state) {
    state.isGoodCategoriesLoading = true;
  },
  [RootMutations.GET_CATEGORIES_SUCCESS](
    state,
    categories: NormalizedScheme<GoodCategory>
  ) {
    state.goodCategories = categories;
    state.isGoodCategoriesLoading = false;
  },
  [RootMutations.GET_CATEGORIES_FAILURE](state) {
    state.isGoodCategoriesLoading = false;
  },
  [RootMutations.SIGN_OUT_REQUEST](state) {
    state.isSignOutLoading = true;
  },
  [RootMutations.SIGN_OUT_SUCCESS](state) {
    state.isSignOutLoading = false;
    state.currentUser = null;
  },
  [RootMutations.SIGN_OUT_FAILURE](state) {
    state.isSignOutLoading = false;
  },
  [RootMutations.UPDATE_CURRENT_USER](state, user: User) {
    state.currentUser = user;
  }
};

export default rootMutations;
