<template>
  <div class="input-text__wrapper">
    <div
      class="input-text__icon-container"
      :class="{ 'p-input-icon-left': !!icon }"
    >
      <i v-if="!!icon" class="pi" :class="{ [icon]: true }" />
      <p-input-text
        :placeholder="placeholder"
        :type="type"
        :name="name"
        :value="inputValue"
        :class="{ 'p-invalid': showError }"
        @input="handleChange"
        @blur="handleBlur"
      ></p-input-text>
    </div>
    <small class="p-error" v-if="showError">{{ errorMessage }}</small>
  </div>
</template>

<style lang="scss" scoped>
.input-text {
  &__wrapper {
    margin-bottom: 1rem;
  }
  &__icon-container {
    width: 100%;
  }
}
.p-error {
  display: block;
  margin-top: 0.25rem;
  font-size: 0.8rem;
  text-align: right;
}
</style>

<style lang="scss">
.p-inputtext {
  width: 100%;
  &.p-invalid:enabled:focus {
    box-shadow: 0 0 0 0.2rem #ffcfcb;
  }
}
</style>

<script lang="ts">
import { useField } from "vee-validate";
import { defineComponent, computed } from "vue";

const UiInputText = defineComponent({
  props: {
    name: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: false,
      default: ""
    },
    type: {
      type: String,
      required: false,
      default: "text"
    },
    icon: {
      type: String,
      required: false,
      default: ""
    }
  },
  setup(props) {
    const {
      value: inputValue,
      errorMessage,
      handleBlur,
      handleChange,
      meta
    } = useField(props.name);

    const showError = computed(() => meta.dirty && !meta.valid);

    return {
      handleChange,
      handleBlur,
      errorMessage,
      inputValue,
      meta,
      showError
    };
  }
});

export default UiInputText;
</script>
