<template>
  <div class="p-grid">
    <div class="p-col-12">
      <div class="p-card p-p-4 p-mb-2">
        <slot name="header"></slot>
      </div>
    </div>
    <div class="p-col-3" v-if="hasSidebarContent">
      <div class="p-card p-p-4 p-mr-2 sidebar">
        <slot name="sidebar"></slot>
      </div>
    </div>
    <div :class="[hasSidebarContent ? 'p-col-9' : 'p-col-12']">
      <div class="p-card p-p-4 content">
        <slot name="content"></slot>
      </div>
    </div>
    <div class="p-col-12">
      <div class="p-card p-p-4 p-mt-2">
        <slot name="footer"></slot>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">
.sidebar,
.content {
  height: 100%;
}
</style>

<script lang="ts">
import { Options, Vue } from "vue-class-component";

@Options({
  computed: {
    hasSidebarContent() {
      return !!this.$slots.sidebar;
    }
  }
})
export default class BaseLayout extends Vue {}
</script>
