<template>
  <unauthorized-layout>
    <template #content>
      <div class="p-grid">
        <div class="p-col-4 p-offset-4">
          <ui-field-set :legend="'Sign In'">
            <ui-progress-spinner v-if="isLoading" />

            <ui-form
              :validation-schema="schema"
              v-slot="{ meta }"
              @submit="handleFormSubmit"
            >
              <ui-input-text
                :name="'email'"
                :placeholder="'Email'"
                :type="'email'"
                :icon="'pi-envelope'"
              />
              <ui-input-text
                :name="'password'"
                :placeholder="'Password'"
                :type="'password'"
                :icon="'pi-lock'"
              />

              <ui-button
                :label="'Submit'"
                :type="'submit'"
                :icon="'pi-check'"
                :disabled="!meta.valid"
              />
              <p class="p-mt-3 p-text-center">
                Don't have an account yet?
                <router-link to="/sign-up">Sign up</router-link>
              </p>
            </ui-form>
          </ui-field-set>
        </div>
      </div>
    </template>
  </unauthorized-layout>
</template>

<style lang="scss" scoped>
fieldset {
  position: relative;
}
button {
  display: block;
  width: 100%;
}
</style>

<script lang="ts">
import { computed, defineComponent, inject } from "vue";

import { SignInFormValue, signInValidationSchema } from "./sign-in.model";

import UnauthorizedLayout from "@/components/layout/unauthorized-layout.vue";
import UiFieldSet from "@/components/ui/ui-field-set.vue";
import UiForm from "@/components/ui/ui-form.vue";
import UiInputText from "@/components/ui/ui-input-text.vue";
import UiButton from "@/components/ui/ui-button.vue";
import UiProgressSpinner from "@/components/ui/ui-progress-spinner.vue";
import { RootActions } from "@/store/actions";
import { ShowMessageFunc, SHOW_MESSAGE } from "@/models/notification.model";
import { useStore } from "vuex";
import { AppStore } from "@/store/interfaces";
import { User } from "@/models/user.model";
import { useRouter } from "vue-router";
import { getRootRouteForRole } from "@/models/router.model";

const SignIn = defineComponent({
  components: {
    UnauthorizedLayout,
    UiFieldSet,
    UiForm,
    UiInputText,
    UiButton,
    UiProgressSpinner
  },
  setup() {
    const showMessage = inject(SHOW_MESSAGE) as ShowMessageFunc;
    const store = useStore() as AppStore;
    const router = useRouter();

    const isLoading = computed(() => store.state.isSignInLoading);

    const handleFormSubmit = async (formValue: SignInFormValue) => {
      try {
        const user: User = await store.dispatch(RootActions.SIGN_IN, formValue);
        router.push(getRootRouteForRole(user.role));
      } catch (error) {
        showMessage({
          type: "failure",
          title: "Cannot sign in",
          message: error.message
        });
      }
    };

    return {
      schema: signInValidationSchema,
      showMessage,
      isLoading,
      handleFormSubmit
    };
  }
});

export default SignIn;
</script>
