import firebaseApp from "./fir.app";

export interface FirSignUpDto {
  email: string;
  password: string;
  displayName: string;
}
export interface FirSignInDto {
  email: string;
  password: string;
}

export interface FirAuthStateListener {
  (currentUserId: string | null): void;
}

export const firSetupAuthStateListener = (
  listener: FirAuthStateListener
): Function =>
  firebaseApp.auth().onAuthStateChanged(user => {
    const currentUserId = user?.uid || null;
    listener(currentUserId);
  });

export const firSignUp = async ({
  email,
  password,
  displayName
}: FirSignUpDto): Promise<string | null> => {
  const { user } = await firebaseApp
    .auth()
    .createUserWithEmailAndPassword(email, password);
  if (!user) {
    return null;
  }

  await user.updateProfile({ displayName });

  return user.uid;
};

export const firSignIn = async ({
  email,
  password
}: FirSignInDto): Promise<string | null> => {
  const { user } = await firebaseApp
    .auth()
    .signInWithEmailAndPassword(email, password);
  if (!user) {
    return null;
  }
  return user.uid;
};

export const firSignOut = async () => {
  await firebaseApp.auth().signOut();
};
