import { NormalizedScheme, removeEntityById } from "@/core/utils/normalize";
import { Good, GoodInCart } from "@/models/good.model";
import { Purchase } from "@/models/purchase.model";
import { cloneDeep } from "lodash";
import { MutationTree } from "vuex";
import { BuyerState } from "./interfaces";

export enum BuyerMutations {
  GET_GOODS_REQUEST = "buyerGetGoodsRequest",
  GET_GOODS_SUCCESS = "buyerGetGoodsSuccess",
  GET_GOODS_FAILURE = "buyerGetGoodsFailure",
  SET_FILTER_BY_CATEGORY = "buyerSetFilterByCategory",
  SET_LOADING_ADD_TO_FAVOURITES = "buyerSetLoadingAddToFavourites",
  SET_DELETE_FROM_FAVOURITES_LOADING = "buyerSetDeleteFromFavouritesLoading",
  RESET_GOODS_LIST = "buyerResetGoodsList",
  GET_FAVOURITES_REQUEST = "buyerGetFavouritesRequest",
  GET_FAVOURITES_SUCCESS = "buyerGetFavouritesSuccess",
  GET_FAVOURITES_FAILURE = "buyerGetFavouritesFailure",
  ADD_GOOD_TO_CART = "buyerAddGoodToCart",
  REMOVE_GOOD_FROM_CART = "buyerRemoveGoodFromCart",
  GET_CART_LIST_REQUEST = "buyerGetCartListRequest",
  GET_CART_LIST_SUCCESS = "buyerGetCartListSuccess",
  GET_CART_LIST_FAILURE = "buyerGetCartListFailure",
  SET_LOADING_PURCHASE_INIT = "buyerSetLoadinPurchaseInit",
  SET_LOADING_PURCHASE_COMPLETE = "buyerSetLoadinPurchaseComplete",
  RESET_CART = "buyerResetCart",
  GET_PURCHASES_REQUEST = "buyerGetPurchasesRequest",
  GET_PURCHASES_SUCCESS = "buyerGetPurchasesSuccess",
  GET_PURCHASES_FAILURE = "buyerGetPurchasesFailure"
}

const buyerMutations: MutationTree<BuyerState> = {
  [BuyerMutations.GET_GOODS_REQUEST](state) {
    state.isGoodsListLoading = true;
  },
  [BuyerMutations.GET_GOODS_SUCCESS](state, goods: NormalizedScheme<Good>) {
    state.goodsList = {
      all: [...state.goodsList.all, ...goods.all],
      byId: {
        ...state.goodsList.byId,
        ...goods.byId
      }
    };

    const lastGood = goods.byId[goods.all[goods.all.length - 1]];
    state.startAfterUpdatedAt = lastGood.updatedAt;

    state.isGoodsListLoading = false;
  },
  [BuyerMutations.GET_GOODS_FAILURE](state) {
    state.isGoodsListLoading = false;
  },
  [BuyerMutations.SET_FILTER_BY_CATEGORY](state, categoryId: string | null) {
    state.filterGoodsByCategoryId = categoryId;
  },
  [BuyerMutations.RESET_GOODS_LIST](state) {
    state.startAfterUpdatedAt = null;
    state.goodsList = {
      all: [],
      byId: {}
    };
  },
  [BuyerMutations.SET_LOADING_ADD_TO_FAVOURITES](state, goodId: string | null) {
    state.loadingAddToFavouritesGoodId = goodId;
  },
  [BuyerMutations.SET_DELETE_FROM_FAVOURITES_LOADING](
    state,
    isLoading: boolean
  ) {
    state.isDeleteFromFavouritesLoading = isLoading;
  },
  [BuyerMutations.GET_FAVOURITES_REQUEST](state) {
    state.isFavouritesListLoading = true;
  },
  [BuyerMutations.GET_FAVOURITES_SUCCESS](
    state,
    goods: NormalizedScheme<Good>
  ) {
    state.favouritesList = goods;
    state.isFavouritesListLoading = false;
  },
  [BuyerMutations.GET_FAVOURITES_FAILURE](state) {
    state.isFavouritesListLoading = false;
  },
  [BuyerMutations.ADD_GOOD_TO_CART](state, goodId: string) {
    const { goodsInCart } = state;
    const nextCart = cloneDeep(goodsInCart);
    if (nextCart.all.includes(goodId)) {
      nextCart.byId[goodId] += 1;
    } else {
      nextCart.all.push(goodId);
      nextCart.byId[goodId] = 1;
    }
    state.goodsInCart = nextCart;
  },
  [BuyerMutations.REMOVE_GOOD_FROM_CART](state, goodId: string) {
    const { goodsInCart } = state;
    if (!goodsInCart.all.includes(goodId)) {
      return;
    }
    let nextCart = cloneDeep(goodsInCart);
    nextCart.byId[goodId] -= 1;
    if (!nextCart.byId[goodId]) {
      nextCart = removeEntityById(goodId, nextCart);
    }
    state.goodsInCart = nextCart;
  },
  [BuyerMutations.GET_CART_LIST_REQUEST](state) {
    state.isGoodsInCartListLoading = true;
  },
  [BuyerMutations.GET_CART_LIST_SUCCESS](
    state,
    goods: NormalizedScheme<GoodInCart>
  ) {
    state.goodsInCartList = goods;
    state.isGoodsInCartListLoading = false;
  },
  [BuyerMutations.GET_CART_LIST_FAILURE](state) {
    state.isGoodsInCartListLoading = false;
  },
  [BuyerMutations.SET_LOADING_PURCHASE_INIT](state, isLoading: boolean) {
    state.isInitPurchaseLoading = isLoading;
  },
  [BuyerMutations.SET_LOADING_PURCHASE_COMPLETE](state, isLoading: boolean) {
    state.isCompletePurchaseLoading = isLoading;
  },
  [BuyerMutations.RESET_CART](state) {
    state.goodsInCart = {
      all: [],
      byId: {}
    };
  },
  [BuyerMutations.GET_PURCHASES_REQUEST](state) {
    state.isPurchasesListLoading = true;
  },
  [BuyerMutations.GET_PURCHASES_SUCCESS](
    state,
    purchases: NormalizedScheme<Purchase>
  ) {
    state.purchasesList = purchases;
    state.isPurchasesListLoading = false;
  },
  [BuyerMutations.GET_PURCHASES_FAILURE](state) {
    state.isPurchasesListLoading = false;
  }
};

export default buyerMutations;
