import { RouterMeta } from "@/models/router.model";
import { UserRole } from "@/models/user.model";
import { RouteRecordRaw } from "vue-router";

export const sellerRoutes: RouteRecordRaw = {
  path: "/seller",
  component: () =>
    import(/* webpackChunkName: "seller" */ "@/views/seller/seller-root.vue"),
  children: [
    {
      path: "",
      redirect: {
        path: "/seller/goods"
      }
    },
    {
      path: "goods",
      strict: true,
      component: () =>
        import(
          /* webpackChunkName: "seller" */ "@/views/seller/good-table/good-table.vue"
        ),
      meta: {
        authorizedOnly: true,
        forRoles: [UserRole.SELLER]
      } as RouterMeta
    },
    {
      path: "goods/:id",
      component: () =>
        import(
          /* webpackChunkName: "seller" */ "@/views/seller/good-create-edit/good-create-edit.vue"
        ),
      meta: {
        authorizedOnly: true,
        forRoles: [UserRole.SELLER]
      } as RouterMeta
    },
    {
      path: "sales",
      component: () =>
        import(
          /* webpackChunkName: "seller" */ "@/views/seller/sales/sales.vue"
        ),
      meta: {
        authorizedOnly: true,
        forRoles: [UserRole.SELLER]
      } as RouterMeta
    }
  ]
};
