import { RouteMeta } from "vue-router";
import { User, UserRole } from "./user.model";

export interface RouterMeta extends RouteMeta {
  authorizedOnly?: boolean;
  unauthorizedOnly?: boolean;
  forRoles?: UserRole[];
}

export interface RouterLinkDescriptor {
  title: string;
  link: string;
  icon?: string;
}

export interface RouterCheckRedirectsDto {
  authorizedOnly?: boolean;
  unauthorizedOnly?: boolean;
  currentUser: User | null;
  forRoles?: UserRole[];
}

export const getRootRouteForRole = (role: UserRole): string => {
  const rootRoutes: Record<UserRole, string> = {
    [UserRole.BUYER]: "/buyer/goods",
    [UserRole.SELLER]: "/seller/goods"
  };
  return rootRoutes[role];
};
