
import { defineComponent, provide } from "vue";
import { useToast } from "primevue/usetoast";
import {
  NotificationType,
  ShowMessageFunc,
  ShowNotificationDto,
  SHOW_MESSAGE
} from "@/models/notification.model";
import Toast from "primevue/toast";
import ConfirmDialog from "primevue/confirmdialog";
import { useConfirm } from "primevue/useconfirm";
import { RootActions } from "./store/actions";
import {
  ConfirmationDto,
  ASK_CONFIRMATION,
  AskConfirmationFunc
} from "@/models/confirmation.model";
import { useStore } from "vuex";

export default defineComponent({
  components: { Toast, ConfirmDialog },
  setup() {
    const toast = useToast();

    const severitiesMap = new Map<NotificationType, string>([
      ["success", "success"],
      ["info", "info"],
      ["warning", "warn"],
      ["failure", "error"]
    ]);
    const defaultSummariesMap = new Map<NotificationType, string>([
      ["success", "Success!"],
      ["info", "Attention!"],
      ["warning", "Warning"],
      ["failure", "Error"]
    ]);

    const showMessage: ShowMessageFunc = ({
      message,
      type,
      title
    }: ShowNotificationDto) => {
      toast.add({
        severity: severitiesMap.get(type),
        summary: title || defaultSummariesMap.get(type),
        detail: message,
        life: 3000
      });
    };

    provide(SHOW_MESSAGE, showMessage);

    const confirm = useConfirm();
    const askConfirmation: AskConfirmationFunc = (dto: ConfirmationDto) => {
      confirm.require(dto);
    };
    provide(ASK_CONFIRMATION, askConfirmation);

    const store = useStore();
    store.dispatch(RootActions.CHECK_INITIAL_USER_STATUS);
    store.dispatch(RootActions.GET_GOOD_CATEGORIES);

    return { showMessage };
  }
});
