import { omit } from "lodash";
import { schema, normalize as normalizeRaw } from "normalizr";

export type SchemeId = string;

export type NormalizedSchemeField<T> = { [id in SchemeId]: T };

export class NormalizedScheme<T> {
  constructor(public byId: NormalizedSchemeField<T>, public all: SchemeId[]) {}
}

const getCommonEntitySchema = (id = "id") =>
  new schema.Entity(
    "byId",
    {},
    {
      idAttribute: id
    }
  );

export const normalize = <T>(
  entities: T[],
  scheme = getCommonEntitySchema()
): NormalizedScheme<T> => {
  const {
    result,
    entities: { byId }
  } = normalizeRaw(entities, [scheme]);

  return {
    all: result,
    byId: byId || {}
  };
};

export const removeEntityById = <T extends NormalizedScheme<P>, P>(
  id: SchemeId | SchemeId[],
  normalized: T
): T =>
  ({
    all: normalized.all.filter(i => i !== id),
    byId: omit(normalized.byId, id)
  } as T);
