import { UserRole } from "@/models/user.model";
import { object, string, SchemaOf, ref, mixed } from "yup";

export interface SignUpFormValue {
  name: string;
  email: string;
  password: string;
  confirmPassword: string;
  role: UserRole;
}

const MIN_PASSWORD_LENGTH = 8;

export const signUpValidationSchema: SchemaOf<SignUpFormValue> = object().shape(
  {
    name: string().required("Display name is required"),
    email: string()
      .email("Incorrect email format")
      .required("Email is required"),
    password: string()
      .min(
        MIN_PASSWORD_LENGTH,
        `Should be at least ${MIN_PASSWORD_LENGTH} characters`
      )
      .required("Password is required"),
    confirmPassword: string()
      .oneOf([ref("password")], "Passwords do not match")
      .required("Confirmation is required"),
    role: mixed<UserRole>()
      .oneOf(Object.values(UserRole))
      .required()
  }
);
