import firebaseApp from "./fir.app";

export interface FirCreateCheckoutSessionDto {
  items: {
    goodId: string;
    quantity: number;
  }[];
}

export interface FirCreateCheckoutSessionResponse {
  sessionId: string;
}

export const firCreateCheckoutSession = async (
  dto: FirCreateCheckoutSessionDto
) => {
  const func = firebaseApp.functions().httpsCallable("createCheckoutSession");
  const response = await func(dto);
  return response.data as FirCreateCheckoutSessionResponse;
};
