import { RouterMeta } from "@/models/router.model";
import { UserRole } from "@/models/user.model";
import { RouteRecordRaw } from "vue-router";

export const buyerRoutes: RouteRecordRaw = {
  path: "/buyer",
  component: () =>
    import(/* webpackChunkName: "buyer" */ "@/views/buyer/buyer-root.vue"),
  children: [
    {
      path: "",
      redirect: {
        path: "/buyer/goods"
      }
    },
    {
      path: "goods",
      component: () =>
        import(
          /* webpackChunkName: "buyer" */ "@/views/buyer/good-list/good-list.vue"
        ),
      meta: {
        authorizedOnly: true,
        forRoles: [UserRole.BUYER]
      } as RouterMeta
    },
    {
      path: "favourites",
      component: () =>
        import(
          /* webpackChunkName: "buyer" */ "@/views/buyer/favourites/favourites.vue"
        ),
      meta: {
        authorizedOnly: true,
        forRoles: [UserRole.BUYER]
      } as RouterMeta
    },
    {
      path: "cart",
      component: () =>
        import(/* webpackChunkName: "buyer" */ "@/views/buyer/cart/cart.vue"),
      meta: {
        authorizedOnly: true,
        forRoles: [UserRole.BUYER]
      } as RouterMeta
    },
    {
      path: "payment-success",
      component: () =>
        import(
          /* webpackChunkName: "buyer" */ "@/views/buyer/payment-success/payment-success.vue"
        ),
      meta: {
        authorizedOnly: true,
        forRoles: [UserRole.BUYER]
      } as RouterMeta
    },
    {
      path: "payment-cancelled",
      component: () =>
        import(
          /* webpackChunkName: "buyer" */ "@/views/buyer/payment-cancelled/payment-cancelled.vue"
        ),
      meta: {
        authorizedOnly: true,
        forRoles: [UserRole.BUYER]
      } as RouterMeta
    },
    {
      path: "purchases",
      component: () =>
        import(
          /* webpackChunkName: "buyer" */ "@/views/buyer/purchases/purchases.vue"
        ),
      meta: {
        authorizedOnly: true,
        forRoles: [UserRole.BUYER]
      } as RouterMeta
    }
  ]
};
