import { GoodCategory } from "@/models/good-category.model";
import { Good } from "@/models/good.model";
import { Purchase } from "@/models/purchase.model";
import { User } from "@/models/user.model";
import { omit } from "lodash";
import firebaseApp from "./fir.app";

export type FirSaveUserDataDto = User;

export type FirSaveGoodDataDto = Omit<Good, "id">;

export interface FirGetGoodsDto {
  uploaderId?: string;
}

export interface FirGetGoodsPortionDto {
  startAfter: number | null;
  limit?: number;
  categoryId?: string | null;
}

export interface FirGetPurchasesDto {
  uploaderId?: string;
  buyerId?: string;
}

const USER_REF = "users";
const CATEGORIES_REF = "categories";
const GOOD_REF = "goods";
const PURCHASE_REF = "purchases";

export const firSaveUserData = async (
  dto: FirSaveUserDataDto
): Promise<User> => {
  await firebaseApp
    .firestore()
    .collection(USER_REF)
    .add(dto);
  return dto;
};

export const firGetUserData = async (userId: string): Promise<User | null> => {
  const snapshot = await firebaseApp
    .firestore()
    .collection(USER_REF)
    .where("id", "==", userId)
    .get();
  if (!snapshot.docs.length) {
    return null;
  }

  return snapshot.docs[0].data() as User;
};

export const firUpdateUserData = async (user: User): Promise<User | null> => {
  const snapshot = await firebaseApp
    .firestore()
    .collection(USER_REF)
    .where("id", "==", user.id)
    .get();
  if (!snapshot.docs.length) {
    return null;
  }
  await snapshot.docs[0].ref.set(user);
  return user;
};

export const firGetGoodCategories = async (): Promise<GoodCategory[]> => {
  const snapshot = await firebaseApp
    .firestore()
    .collection(CATEGORIES_REF)
    .get();
  return snapshot.docs.map(doc => {
    const { id } = doc;
    const { title } = doc.data() as { title: string };
    return { id, title };
  });
};

export const firSaveGoodData = async (
  dto: FirSaveGoodDataDto
): Promise<Good> => {
  const { id } = await firebaseApp
    .firestore()
    .collection(GOOD_REF)
    .add(dto);
  return {
    id,
    ...dto
  };
};

export const firUpdateGoodData = async (good: Good): Promise<Good> => {
  const snapshot = await firebaseApp
    .firestore()
    .collection(GOOD_REF)
    .doc(good.id);
  await snapshot.update(omit(good, "id"));
  return good;
};

export const firGetGoods = async (
  dto: FirGetGoodsDto = {}
): Promise<Good[]> => {
  const { uploaderId } = dto;
  const collection = firebaseApp
    .firestore()
    .collection(GOOD_REF)
    .where("uploaderId", "==", uploaderId)
    .orderBy("title");

  const snapshot = await collection.get();
  return snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Good));
};

export const firGetGoodsPortion = async (
  dto: FirGetGoodsPortionDto
): Promise<Good[]> => {
  let collection = firebaseApp
    .firestore()
    .collection(GOOD_REF)
    .orderBy("updatedAt", "desc");

  if (dto.startAfter) {
    collection = collection.startAfter(dto.startAfter);
  }
  if (dto.limit) {
    collection = collection.limit(dto.limit);
  }
  if (dto.categoryId) {
    collection = collection.where("categoryId", "==", dto.categoryId);
  }

  const snapshot = await collection.get();
  return snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Good));
};

export const firGetGoodData = async (goodId: string): Promise<Good | null> => {
  const snapshot = await firebaseApp
    .firestore()
    .collection(GOOD_REF)
    .doc(goodId)
    .get();
  const goodData = snapshot.data();
  if (!goodData) {
    return null;
  }

  return {
    id: goodId,
    ...goodData
  } as Good;
};

export const firDeleteGood = async (goodId: string): Promise<void> => {
  await firebaseApp
    .firestore()
    .collection(GOOD_REF)
    .doc(goodId)
    .delete();
};

export const firSavePurchaseData = async (
  data: Omit<Purchase, "id">
): Promise<Purchase> => {
  const { id } = await firebaseApp
    .firestore()
    .collection(PURCHASE_REF)
    .add(data);
  return {
    id,
    ...data
  };
};

export const firGetPurchases = async (
  dto: FirGetPurchasesDto
): Promise<Purchase[]> => {
  const { uploaderId, buyerId } = dto;
  let collection = firebaseApp
    .firestore()
    .collection(PURCHASE_REF)
    .orderBy("buyedAt");

  if (uploaderId) {
    collection = collection.where("uploaderId", "==", uploaderId);
  }
  if (buyerId) {
    collection = collection.where("buyerId", "==", buyerId);
  }

  const snapshot = await collection.get();
  return snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Purchase));
};
