import { getFirErrorMessage } from "@/core/firebase/fir.core";
import {
  firGetGoodData,
  firGetGoodsPortion,
  firGetPurchases,
  firSavePurchaseData,
  firUpdateUserData
} from "@/core/firebase/fir.firestore";
import {
  firCreateCheckoutSession,
  FirCreateCheckoutSessionDto
} from "@/core/firebase/fir.functions";
import {
  normalize,
  NormalizedScheme,
  removeEntityById
} from "@/core/utils/normalize";
import { Good, GoodInCart } from "@/models/good.model";
import { Purchase } from "@/models/purchase.model";
import { RootState } from "@/store/interfaces";
import { RootMutations } from "@/store/mutations";
import { cloneDeep } from "lodash";
import { ActionTree } from "vuex";
import { BuyerGetters } from "./getters";
import { BuyerAddToFavouritesResult, BuyerState } from "./interfaces";
import { BuyerMutations } from "./mutations";

export enum BuyerActions {
  GET_NEXT_GOODS_PORTION = "buyerGetNextGoodsPortion",
  ADD_TO_FAVOURITES = "buyerAddToFavourites",
  GET_FAVOURITES_LIST = "buyerGetFavouritesList",
  DELETE_FROM_FAVOURITES = "buyerDeleteFromFavourites",
  GET_GOODS_IN_CART = "buyerGetGoodsInCart",
  INIT_PURCHASE = "buyerInitPurchase",
  COMPLETE_PURCHASE = "buyerCompletePurchase",
  GET_PURCHASES_LIST = "buyerGetPurchasesList"
}

const buyerActions: ActionTree<BuyerState, RootState> = {
  [BuyerActions.GET_NEXT_GOODS_PORTION]: async ({ commit, state }) => {
    commit(BuyerMutations.GET_GOODS_REQUEST);

    const { startAfterUpdatedAt, filterGoodsByCategoryId } = state;
    let goods: Good[];

    try {
      goods = await firGetGoodsPortion({
        startAfter: startAfterUpdatedAt,
        limit: 9,
        categoryId: filterGoodsByCategoryId
      });
    } catch (e) {
      commit(BuyerMutations.GET_GOODS_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    const normalizedGoods = normalize(goods);
    commit(BuyerMutations.GET_GOODS_SUCCESS, normalizedGoods);
  },
  [BuyerActions.ADD_TO_FAVOURITES]: async (
    { commit, rootState },
    goodId: string
  ): Promise<BuyerAddToFavouritesResult> => {
    const { currentUser } = rootState;
    if (!currentUser) {
      return {
        isAdded: false
      };
    }

    if (currentUser.favourites?.includes(goodId)) {
      return {
        isAdded: false,
        reason: "You already have this good in your favourites list"
      };
    }

    commit(BuyerMutations.SET_LOADING_ADD_TO_FAVOURITES, goodId);

    const nextUser = cloneDeep(currentUser);
    if (!nextUser.favourites) {
      nextUser.favourites = [];
    }
    nextUser.favourites.push(goodId);

    let isUpdated = false;
    try {
      isUpdated = !!(await firUpdateUserData(nextUser));
    } catch (e) {
      commit(BuyerMutations.SET_LOADING_ADD_TO_FAVOURITES, null);
      throw new Error(getFirErrorMessage(e));
    }

    if (!isUpdated) {
      throw new Error("User not found");
    }

    commit(BuyerMutations.SET_LOADING_ADD_TO_FAVOURITES, null);
    commit(RootMutations.UPDATE_CURRENT_USER, nextUser);

    return {
      isAdded: true
    };
  },
  [BuyerActions.GET_FAVOURITES_LIST]: async ({ commit, rootState }) => {
    const { currentUser } = rootState;
    if (!currentUser?.favourites?.length) {
      return;
    }

    commit(BuyerMutations.GET_FAVOURITES_REQUEST);

    const promises = currentUser.favourites.map(id => firGetGoodData(id));
    let goods: (Good | null)[];

    try {
      goods = await Promise.all(promises);
    } catch (e) {
      commit(BuyerMutations.GET_FAVOURITES_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    // good could've been deleted after being added to favourites
    if (goods.includes(null)) {
      goods = goods.filter(good => !!good);

      const nextUser = cloneDeep(currentUser);
      nextUser.favourites = (goods as Good[]).map(({ id }) => id);
      commit(RootMutations.UPDATE_CURRENT_USER, nextUser);
    }

    commit(BuyerMutations.GET_FAVOURITES_SUCCESS, normalize(goods));
  },
  [BuyerActions.DELETE_FROM_FAVOURITES]: async (
    { rootState, state, commit },
    goodId: string
  ) => {
    const { currentUser } = rootState;
    if (
      !currentUser ||
      !currentUser.favourites ||
      !currentUser.favourites.includes(goodId)
    ) {
      return;
    }

    commit(BuyerMutations.SET_DELETE_FROM_FAVOURITES_LOADING, true);

    const nextUser = cloneDeep(currentUser);
    nextUser.favourites = (nextUser.favourites as string[]).filter(
      id => id !== goodId
    );

    let isUpdated = false;
    try {
      isUpdated = !!(await firUpdateUserData(nextUser));
    } catch (e) {
      commit(BuyerMutations.SET_DELETE_FROM_FAVOURITES_LOADING, false);
      throw new Error(getFirErrorMessage(e));
    }

    if (!isUpdated) {
      throw new Error("User not found");
    }

    const { favouritesList } = state;
    const nextFavouritesList = removeEntityById(goodId, favouritesList);

    commit(BuyerMutations.SET_DELETE_FROM_FAVOURITES_LOADING, false);
    commit(BuyerMutations.GET_FAVOURITES_SUCCESS, nextFavouritesList);
    commit(RootMutations.UPDATE_CURRENT_USER, nextUser);
  },
  [BuyerActions.GET_GOODS_IN_CART]: async ({ state, commit }) => {
    commit(BuyerMutations.GET_CART_LIST_REQUEST);

    const { goodsInCart } = state;
    const promises = goodsInCart.all.map(id => firGetGoodData(id));
    let goods: (Good | null)[];

    try {
      goods = await Promise.all(promises);
    } catch (e) {
      commit(BuyerMutations.GET_CART_LIST_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    if (goods.includes(null)) {
      goods = goods.filter(good => !!good);
    }

    commit(BuyerMutations.GET_CART_LIST_SUCCESS, normalize(goods));
  },
  [BuyerActions.INIT_PURCHASE]: async ({
    commit,
    getters
  }): Promise<string> => {
    commit(BuyerMutations.SET_LOADING_PURCHASE_INIT, true);
    const goods = getters[BuyerGetters.CART_PRESENTATION] as NormalizedScheme<
      GoodInCart
    >;

    const dto: FirCreateCheckoutSessionDto = {
      items: goods.all.map(id => ({
        goodId: id,
        quantity: goods.byId[id].quantity
      }))
    };

    let sessionId: string;
    try {
      sessionId = (await firCreateCheckoutSession(dto)).sessionId;
    } catch (e) {
      commit(BuyerMutations.SET_LOADING_PURCHASE_INIT, false);
      throw new Error(getFirErrorMessage(e));
    }

    commit(BuyerMutations.SET_LOADING_PURCHASE_INIT, false);
    return sessionId;
  },
  [BuyerActions.COMPLETE_PURCHASE]: async ({ state, commit, rootState }) => {
    const { goodsInCart } = state;
    if (!goodsInCart.all.length) {
      return;
    }

    commit(BuyerMutations.RESET_CART);
    commit(BuyerMutations.SET_LOADING_PURCHASE_COMPLETE, true);

    const promises = goodsInCart.all.map(async goodId => {
      const good = await firGetGoodData(goodId);
      if (!good) {
        throw new Error("Some of goods in cart was not found");
      }

      const purchaseData: Omit<Purchase, "id"> = {
        buyedAt: Date.now(),
        buyerId: rootState.currentUser!.id,
        categoryId: good.categoryId,
        cost: good.cost,
        image: good.image,
        quantity: goodsInCart.byId[goodId],
        title: good.title,
        uploaderId: good.uploaderId
      };
      await firSavePurchaseData(purchaseData);
    });

    try {
      await Promise.all(promises);
      commit(BuyerMutations.SET_LOADING_PURCHASE_COMPLETE, false);
    } catch (e) {
      commit(BuyerMutations.SET_LOADING_PURCHASE_COMPLETE, false);
      throw new Error(getFirErrorMessage(e));
    }
  },
  [BuyerActions.GET_PURCHASES_LIST]: async ({ commit, rootState }) => {
    commit(BuyerMutations.GET_PURCHASES_REQUEST);

    const { currentUser } = rootState;
    let purchases: Purchase[];

    try {
      purchases = await firGetPurchases({
        buyerId: currentUser!.id
      });
    } catch (e) {
      commit(BuyerMutations.GET_PURCHASES_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    commit(BuyerMutations.GET_PURCHASES_SUCCESS, normalize(purchases));
  }
};

export default buyerActions;
