import { GetterTree } from "vuex";
import { DateTime } from "luxon";
import { SellerState } from "./interfaces";
import { RootState } from "../../interfaces";
import {
  PurchasePresentation,
  PurchaseStatistics
} from "@/models/purchase.model";

export enum SellerGetters {
  SALES_PRESENTATION = "sellerSalesPresentation",
  SALES_STATISTICS = "sellerSalesStatistics"
}

const sellerGetters: GetterTree<SellerState, RootState> = {
  [SellerGetters.SALES_PRESENTATION](state): PurchasePresentation[] {
    return state.salesList.all.map(id => {
      const purchase = state.salesList.byId[id];
      return {
        ...purchase,
        totalCost: purchase.quantity * purchase.cost
      };
    });
  },
  [SellerGetters.SALES_STATISTICS](state, getters): PurchaseStatistics {
    let totalAllTime = 0;
    let totalThisMonth = 0;

    const sales = getters[
      SellerGetters.SALES_PRESENTATION
    ] as PurchasePresentation[];
    const startOfMonth = DateTime.now()
      .startOf("month")
      .toMillis();

    sales.forEach(({ buyedAt, totalCost }) => {
      totalAllTime += totalCost;
      if (buyedAt > startOfMonth) {
        totalThisMonth += totalCost;
      }
    });

    return { totalAllTime, totalThisMonth };
  }
};

export default sellerGetters;
