<template>
  <div class="home p-d-flex p-ai-center p-jc-center">
    <img src="../assets/images/title.svg" />
  </div>
</template>

<style lang="scss">
@keyframes noise {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 100% 100%;
  }
}
</style>

<style lang="scss" scoped>
.home {
  background: url("../assets/images/noise.jpg");
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  animation: noise 0.5s steps(10) infinite;
}
</style>

<script lang="ts">
import { AppStore } from "@/store/interfaces";
import { useStore } from "vuex";
import { defineComponent, onMounted } from "vue";
import { useRouter } from "vue-router";
import { getRootRouteForRole } from "@/models/router.model";

const Home = defineComponent({
  setup() {
    const store = useStore() as AppStore;
    const router = useRouter();

    onMounted(() => {
      setTimeout(() => {
        const { currentUser } = store.state;
        const path = currentUser
          ? getRootRouteForRole(currentUser.role)
          : "/sign-in";
        router.push(path);
      }, 2000);
    });
  }
});

export default Home;
</script>
