import { currencyFormat } from "@/composables/useCurrencyFormat";
import { NormalizedScheme } from "@/core/utils/normalize";
import { GoodCategory } from "@/models/good-category.model";
import { GoodInCart, GoodPresentation } from "@/models/good.model";
import { PurchasePresentation } from "@/models/purchase.model";
import { RootState } from "@/store/interfaces";
import { GetterTree } from "vuex";
import { BuyerState } from "./interfaces";

export enum BuyerGetters {
  GOODS_PRESENTATION = "buyerGoodsPresentation",
  CART_TOTAL_GOODS_AMOUNT = "buyerCartTotalGoodsAmount",
  CART_TOTAL_COST = "buyerCartTodalCost",
  CART_PRESENTATION = "buyerCartPresentation",
  PURCHASES_PRESENTATION = "buyerPurchasesPresentation"
}

const getCategoriesPalette = (categories: NormalizedScheme<GoodCategory>) => {
  const palette = [
    "#FF0000",
    "#FFA500",
    "#FFFF00",
    "#008000",
    "#00FFFF",
    "#0000FF",
    "#7F00FF"
  ];
  const map = new Map<string, string>();
  categories.all.forEach((categoryId, i) => {
    map.set(categoryId, palette[i % palette.length]);
  });
  return map;
};

const buyerGetters: GetterTree<BuyerState, RootState> = {
  [BuyerGetters.GOODS_PRESENTATION](
    state,
    getters,
    rootState
  ): GoodPresentation[] {
    const { goodCategories, currentUser } = rootState;
    const palette = getCategoriesPalette(goodCategories);
    return state.goodsList.all.map(goodId => {
      const good = state.goodsList.byId[goodId];
      const category = goodCategories.byId[good.categoryId];
      return {
        ...good,
        categoryTitle: category ? category.title : "",
        categoryColor: category ? palette.get(category.id) : "",
        formattedCost: currencyFormat(good.cost),
        canAddToFavourites: !currentUser?.favourites?.includes(goodId),
        isAddToFavouritesLoading: state.loadingAddToFavouritesGoodId === goodId
      };
    });
  },
  [BuyerGetters.CART_TOTAL_GOODS_AMOUNT](state): number {
    return state.goodsInCart.all
      .map(id => state.goodsInCart.byId[id])
      .reduce((acc, curr) => acc + curr, 0);
  },
  [BuyerGetters.CART_PRESENTATION](state): NormalizedScheme<GoodInCart> {
    const { goodsInCart, goodsInCartList } = state;
    const scheme: NormalizedScheme<GoodInCart> = {
      all: [],
      byId: {}
    };
    goodsInCartList.all
      .filter(id => goodsInCart.all.includes(id))
      .forEach(id => {
        scheme.all.push(id);
        scheme.byId[id] = {
          ...goodsInCartList.byId[id],
          quantity: goodsInCart.byId[id]
        };
      });
    return scheme;
  },
  [BuyerGetters.CART_TOTAL_COST](state, getters): number {
    const cartPresentation = getters[
      BuyerGetters.CART_PRESENTATION
    ] as NormalizedScheme<GoodInCart>;
    return cartPresentation.all
      .map(id => {
        const { quantity, cost } = cartPresentation.byId[id];
        return quantity * cost;
      })
      .reduce((acc, curr) => acc + curr, 0);
  },
  [BuyerGetters.PURCHASES_PRESENTATION](state): PurchasePresentation[] {
    return state.purchasesList.all.map(id => {
      const purchase = state.purchasesList.byId[id];
      return {
        ...purchase,
        totalCost: purchase.quantity * purchase.cost
      };
    });
  }
};

export default buyerGetters;
