import { Good } from "@/models/good.model";
import { RootState } from "@/store/interfaces";
import { GoodCreateEditFormValue } from "@/views/seller/good-create-edit/good-create-edit.model";
import { ActionTree } from "vuex";
import { SellerState } from "./interfaces";
import { SellerMutations } from "./mutations";
import { getFirErrorMessage } from "@/core/firebase/fir.core";
import {
  firDeleteGood,
  firGetGoodData,
  firGetGoods,
  firGetPurchases,
  firSaveGoodData,
  FirSaveGoodDataDto,
  firUpdateGoodData
} from "@/core/firebase/fir.firestore";
import { normalize, removeEntityById } from "@/core/utils/normalize";
import { Purchase } from "@/models/purchase.model";

export enum SellerActions {
  CREATE_GOOD = "sellerCreateGood",
  GET_MY_GOODS = "sellerGetMyGoods",
  GET_GOOD_TO_EDIT = "sellerGetGoodToEdit",
  EDIT_GOOD = "sellerEditGood",
  DELETE_GOOD = "sellerDeleteGood",
  GET_SALES_LIST = "sellerGetSalesList"
}

const sellerActions: ActionTree<SellerState, RootState> = {
  [SellerActions.CREATE_GOOD]: async (
    { commit, rootState },
    dto: GoodCreateEditFormValue
  ): Promise<Good> => {
    commit(SellerMutations.CREATE_GOOD_REQUEST);

    const newGoodDto: FirSaveGoodDataDto = {
      createdAt: Date.now(),
      updatedAt: Date.now(),
      uploaderId: rootState.currentUser!.id,
      ...dto
    };

    let newGood: Good;
    try {
      newGood = await firSaveGoodData(newGoodDto);
    } catch (e) {
      commit(SellerMutations.CREATE_GOOD_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    commit(SellerMutations.CREATE_GOOD_SUCCESS);
    return newGood;
  },
  [SellerActions.GET_GOOD_TO_EDIT]: async (
    { commit },
    goodId: string
  ): Promise<Good> => {
    commit(SellerMutations.GET_GOOD_FOR_EDIT_REQUEST);

    let good: Good | null;
    try {
      good = await firGetGoodData(goodId);
    } catch (e) {
      commit(SellerMutations.GET_GOOD_FOR_EDIT_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    if (!good) {
      commit(SellerMutations.GET_GOOD_FOR_EDIT_FAILURE);
      throw new Error("Good not found");
    }

    commit(SellerMutations.GET_GOOD_FOR_EDIT_SUCCESS, good);
    return good;
  },
  [SellerActions.EDIT_GOOD]: async (
    { commit, state },
    dto: GoodCreateEditFormValue
  ): Promise<Good> => {
    commit(SellerMutations.EDIT_GOOD_REQUEST);

    const { goodForEdit } = state;
    if (!goodForEdit) {
      commit(SellerMutations.EDIT_GOOD_FAILURE);
      throw new Error("No good to update");
    }

    const updatedGood: Good = Object.assign(goodForEdit, dto);
    updatedGood.updatedAt = Date.now();
    try {
      await firUpdateGoodData(updatedGood);
    } catch (e) {
      commit(SellerMutations.EDIT_GOOD_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    commit(SellerMutations.EDIT_GOOD_SUCCESS);
    return updatedGood;
  },
  [SellerActions.GET_MY_GOODS]: async ({ commit, rootState }) => {
    commit(SellerMutations.GET_GOODS_REQUEST);

    const uploaderId = rootState.currentUser!.id;

    let goods: Good[];
    try {
      goods = await firGetGoods({
        uploaderId
      });
    } catch (e) {
      commit(SellerMutations.GET_GOODS_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    const normalizedGoods = normalize(goods);
    commit(SellerMutations.GET_GOODS_SUCCESS, normalizedGoods);
  },
  [SellerActions.DELETE_GOOD]: async ({ commit, state }, goodId: string) => {
    commit(SellerMutations.DELETE_GOOD_REQUEST);

    try {
      await firDeleteGood(goodId);
    } catch (e) {
      commit(SellerMutations.DELETE_GOOD_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    const nextGoods = removeEntityById(goodId, state.goodsList);
    commit(SellerMutations.GET_GOODS_SUCCESS, nextGoods);

    commit(SellerMutations.DELETE_GOOD_SUCCESS);
  },
  [SellerActions.GET_SALES_LIST]: async ({ commit, rootState }) => {
    commit(SellerMutations.GET_SALES_REQUEST);

    const { currentUser } = rootState;
    let sales: Purchase[];

    try {
      sales = await firGetPurchases({
        uploaderId: currentUser!.id
      });
    } catch (e) {
      commit(SellerMutations.GET_SALES_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    commit(SellerMutations.GET_SALES_SUCCESS, normalize(sales));
  }
};

export default sellerActions;
