
import { defineComponent } from "vue";

const UiButton = defineComponent({
  props: {
    label: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      required: false
    },
    type: {
      // todo: update to literal type
      type: String,
      required: false,
      default: "button",
      validator: (value: string) => {
        return ["button", "reset", "submit"].includes(value);
      }
    },
    disabled: {
      type: Boolean,
      required: false
    },
    dangerous: {
      type: Boolean,
      required: false,
      default: false
    },
    fluid: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  computed: {
    fullIcon(): string {
      return this.icon ? `pi ${this.icon}` : "";
    }
  },
  emits: ["click"],
  methods: {
    handleClick(event: Event) {
      this.$emit("click", event);
    }
  }
});

export default UiButton;
