import { UiDropdownOptions } from "@/models/dropdown.model";
import { GetterTree } from "vuex";
import { RootState } from "./interfaces";

export enum RootGetters {
  CATEGORIES_OPTIONS = "categoriesOptions"
}

const rootGetters: GetterTree<RootState, RootState> = {
  [RootGetters.CATEGORIES_OPTIONS](state): UiDropdownOptions<string>[] {
    const { goodCategories } = state;
    return goodCategories.all.map(id => ({
      label: goodCategories.byId[id].title,
      value: id
    }));
  }
};

export default rootGetters;
