import {
  RouterCheckRedirectsDto,
  getRootRouteForRole,
  RouterMeta
} from "@/models/router.model";
import { NavigationGuardNext, NavigationGuardWithThis } from "vue-router";
import store from "@/store";
import { RootMutations } from "@/store/mutations";

const checkRedirects = (
  {
    authorizedOnly,
    unauthorizedOnly,
    currentUser,
    forRoles
  }: RouterCheckRedirectsDto,
  next: NavigationGuardNext
) => {
  if (authorizedOnly) {
    if (!currentUser) {
      return next("/sign-in");
    }
    if (forRoles && !forRoles.includes(currentUser.role)) {
      return next(getRootRouteForRole(currentUser.role));
    }
  }
  if (unauthorizedOnly && currentUser) {
    return next(getRootRouteForRole(currentUser.role));
  }
  next();
};

const authGuard: NavigationGuardWithThis<undefined> = (to, from, next) => {
  if (!to.meta) {
    return next();
  }

  const { authorizedOnly, forRoles, unauthorizedOnly } = to.meta as RouterMeta;
  if (!authorizedOnly && !unauthorizedOnly) {
    return next();
  }

  // this logic relies on the fact that App's `created` hook
  // will resolve _before_ this, so if `isCheckStatusLoading`
  // is `false` here - it means status check is _complete_
  const { isCheckStatusLoading, currentUser } = store.state;
  if (isCheckStatusLoading) {
    store.subscribe(mutation => {
      if (mutation.type !== RootMutations.CHECK_INITIAL_USER_STATUS_COMPLETE) {
        return;
      }
      checkRedirects(
        {
          authorizedOnly,
          unauthorizedOnly,
          currentUser: mutation.payload,
          forRoles
        },
        next
      );
    });
    return;
  }

  checkRedirects(
    {
      authorizedOnly,
      unauthorizedOnly,
      currentUser,
      forRoles
    },
    next
  );
};

export default authGuard;
