import {
  firSetupAuthStateListener,
  firSignIn,
  firSignOut,
  firSignUp
} from "@/core/firebase/fir.auth";
import { getFirErrorMessage } from "@/core/firebase/fir.core";
import {
  firGetGoodCategories,
  firGetUserData,
  firSaveUserData
} from "@/core/firebase/fir.firestore";
import { normalize } from "@/core/utils/normalize";
import { GoodCategory } from "@/models/good-category.model";
import { User } from "@/models/user.model";
import { SignInFormValue } from "@/views/sign-in/sign-in.model";
import { SignUpFormValue } from "@/views/sign-up/sign-up.model";
import { ActionTree } from "vuex";
import { RootState } from "./interfaces";
import { RootMutations } from "./mutations";

export enum RootActions {
  SIGN_UP = "signUp",
  SIGN_IN = "signIn",
  CHECK_INITIAL_USER_STATUS = "checkInitialUserStatus",
  SIGN_OUT = "signOut",
  GET_GOOD_CATEGORIES = "getGoodCategories"
}

const rootActions: ActionTree<RootState, RootState> = {
  [RootActions.SIGN_UP]: async (
    { commit },
    dto: SignUpFormValue
  ): Promise<User> => {
    commit(RootMutations.SIGN_UP_REQUEST);

    const { name: displayName, email, password, role } = dto;
    let id: string | null;

    try {
      id = await firSignUp({ displayName, email, password });
    } catch (e) {
      commit(RootMutations.SIGN_UP_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    if (!id) {
      commit(RootMutations.SIGN_UP_FAILURE);
      throw new Error("User UID not obtained");
    }

    const user: User = {
      id,
      displayName,
      role,
      email
    };
    try {
      await firSaveUserData(user);
    } catch (e) {
      commit(RootMutations.SIGN_UP_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    commit(RootMutations.SIGN_UP_SUCCESS, user);
    return user;
  },
  [RootActions.SIGN_IN]: async (
    { commit },
    dto: SignInFormValue
  ): Promise<User> => {
    commit(RootMutations.SIGN_IN_REQUEST);

    const { email, password } = dto;
    let id: string | null;

    try {
      id = await firSignIn({ email, password });
    } catch (e) {
      commit(RootMutations.SIGN_IN_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    if (!id) {
      commit(RootMutations.SIGN_IN_FAILURE);
      throw new Error("User UID not obtained");
    }

    let user: User | null;
    try {
      user = await firGetUserData(id);
    } catch (e) {
      commit(RootMutations.SIGN_UP_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }
    if (!user) {
      commit(RootMutations.SIGN_IN_FAILURE);
      throw new Error("User not obtained");
    }

    commit(RootMutations.SIGN_IN_SUCCESS, user);
    return user;
  },
  [RootActions.CHECK_INITIAL_USER_STATUS]: ({ commit }) => {
    commit(RootMutations.CHECK_INITIAL_USER_STATUS_START);
    const unsubscribe = firSetupAuthStateListener(async currentUserId => {
      if (!currentUserId) {
        commit(RootMutations.CHECK_INITIAL_USER_STATUS_COMPLETE, null);
        return;
      }

      let user: User | null;
      try {
        user = await firGetUserData(currentUserId);
      } catch (e) {
        commit(RootMutations.CHECK_INITIAL_USER_STATUS_COMPLETE, null);
        return;
      }

      commit(RootMutations.CHECK_INITIAL_USER_STATUS_COMPLETE, user);

      unsubscribe();
    });
  },
  [RootActions.SIGN_OUT]: async ({ commit }) => {
    commit(RootMutations.SIGN_OUT_REQUEST);

    try {
      await firSignOut();
    } catch (e) {
      commit(RootMutations.SIGN_OUT_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    commit(RootMutations.SIGN_OUT_SUCCESS);
  },
  [RootActions.GET_GOOD_CATEGORIES]: async ({ commit }) => {
    commit(RootMutations.GET_CATEGORIES_REQUEST);

    let categories: GoodCategory[];
    try {
      categories = await firGetGoodCategories();
    } catch (e) {
      commit(RootMutations.GET_CATEGORIES_FAILURE);
      throw new Error(getFirErrorMessage(e));
    }

    if (!categories.length) {
      commit(RootMutations.GET_CATEGORIES_FAILURE);
      throw new Error("List of good categories is empty");
    }

    commit(RootMutations.GET_CATEGORIES_SUCCESS, normalize(categories));
  }
};

export default rootActions;
