<template>
  <Form
    ref="veeForm"
    :validation-schema="validationSchema"
    v-slot="{ meta, values }"
    @submit="handleSubmit"
  >
    <slot :meta="meta" :values="values" />
  </Form>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import { Form } from "vee-validate";
import { AnyObjectSchema } from "yup";

interface VeeForm {
  setFieldValue: (field: string, value: unknown) => void;
  setValues: (values: object) => void;
  resetForm: () => void;
}

const UiForm = defineComponent({
  components: {
    Form
  },
  props: {
    validationSchema: {
      type: Object as PropType<AnyObjectSchema>,
      required: true
    }
  },
  emits: ["submit"],
  methods: {
    handleSubmit(formValue: object) {
      this.$emit("submit", formValue);
    },
    setFormValue(field: string, value: unknown) {
      (this.$refs.veeForm as VeeForm).setFieldValue(field, value);
    },
    setValues<T extends object>(values: Partial<T>) {
      (this.$refs.veeForm as VeeForm).setValues(values);
    },
    resetForm() {
      (this.$refs.veeForm as VeeForm).resetForm();
    }
  }
});

export default UiForm;
</script>
