
import { computed, defineComponent, inject } from "vue";

import { User, UserRole } from "@/models/user.model";
import { RootActions } from "@/store/actions";
import { signUpValidationSchema, SignUpFormValue } from "./sign-up.model";
import { ShowMessageFunc, SHOW_MESSAGE } from "@/models/notification.model";

import UnauthorizedLayout from "@/components/layout/unauthorized-layout.vue";
import UiFieldSet from "@/components/ui/ui-field-set.vue";
import UiForm from "@/components/ui/ui-form.vue";
import UiInputText from "@/components/ui/ui-input-text.vue";
import UiSelectButton, {
  UiSelectButtonOption
} from "@/components/ui/ui-select-button.vue";
import UiButton from "@/components/ui/ui-button.vue";
import UiProgressSpinner from "@/components/ui/ui-progress-spinner.vue";
import { useStore } from "vuex";
import { AppStore } from "@/store/interfaces";
import { useRouter } from "vue-router";
import { getRootRouteForRole } from "@/models/router.model";

const SignUp = defineComponent({
  components: {
    UnauthorizedLayout,
    UiForm,
    UiFieldSet,
    UiInputText,
    UiSelectButton,
    UiButton,
    UiProgressSpinner
  },
  setup() {
    const showMessage = inject(SHOW_MESSAGE) as ShowMessageFunc;
    const store = useStore() as AppStore;
    const router = useRouter();

    const roleOptions: UiSelectButtonOption<UserRole>[] = [
      {
        label: "I'm going to buy",
        value: UserRole.BUYER
      },
      {
        label: "I'm going to sell",
        value: UserRole.SELLER
      }
    ];

    const isLoading = computed(() => store.state.isSignUpLoading);

    const handleFormSubmit = async (formValue: SignUpFormValue) => {
      try {
        const user: User = await store.dispatch(RootActions.SIGN_UP, formValue);
        showMessage({
          type: "success",
          title: `Hi ${user.displayName}!`,
          message: "Welcome to Hiryuu Meta"
        });
        router.push(getRootRouteForRole(user.role));
      } catch (error) {
        showMessage({
          type: "failure",
          title: "Cannot sign up",
          message: error.message
        });
      }
    };

    return {
      schema: signUpValidationSchema,
      roleOptions,
      showMessage,
      isLoading,
      handleFormSubmit
    };
  }
});

export default SignUp;
