<template>
  <p-button
    :label="label"
    :icon="fullIcon"
    :type="type"
    :disabled="disabled"
    :class="{ 'p-button-danger': dangerous, 'ui-fluid': fluid }"
    @click="handleClick"
  />
</template>

<style lang="scss" scoped>
.p-button {
  &.ui-fluid {
    width: 100%;
  }
}
</style>

<script lang="ts">
import { defineComponent } from "vue";

const UiButton = defineComponent({
  props: {
    label: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      required: false
    },
    type: {
      // todo: update to literal type
      type: String,
      required: false,
      default: "button",
      validator: (value: string) => {
        return ["button", "reset", "submit"].includes(value);
      }
    },
    disabled: {
      type: Boolean,
      required: false
    },
    dangerous: {
      type: Boolean,
      required: false,
      default: false
    },
    fluid: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  computed: {
    fullIcon(): string {
      return this.icon ? `pi ${this.icon}` : "";
    }
  },
  emits: ["click"],
  methods: {
    handleClick(event: Event) {
      this.$emit("click", event);
    }
  }
});

export default UiButton;
</script>
