import { NormalizedScheme } from "@/core/utils/normalize";
import { Good } from "@/models/good.model";
import { Purchase } from "@/models/purchase.model";
import { MutationTree } from "vuex";
import { SellerState } from "./interfaces";

export enum SellerMutations {
  CREATE_GOOD_REQUEST = "sellerCreateGoodRequest",
  CREATE_GOOD_SUCCESS = "sellerCreateGoodSuccess",
  CREATE_GOOD_FAILURE = "sellerCreateGoodFailure",
  EDIT_GOOD_REQUEST = "sellerEditGoodRequest",
  EDIT_GOOD_SUCCESS = "sellerEditGoodSuccess",
  EDIT_GOOD_FAILURE = "sellerEditGoodFailure",
  GET_GOOD_FOR_EDIT_REQUEST = "sellerGetGoodForEditRequest",
  GET_GOOD_FOR_EDIT_SUCCESS = "sellerGetGoodForEditSuccess",
  GET_GOOD_FOR_EDIT_FAILURE = "sellerGetGoodForEditFailure",
  GET_GOODS_REQUEST = "sellerGetGoodsRequest",
  GET_GOODS_SUCCESS = "sellerGetGoodsSuccess",
  GET_GOODS_FAILURE = "sellerGetGoodsFailure",
  DELETE_GOOD_REQUEST = "sellerDeleteGoodRequest",
  DELETE_GOOD_SUCCESS = "sellerDeleteGoodSuccess",
  DELETE_GOOD_FAILURE = "sellerDeleteGoodFailure",
  GET_SALES_REQUEST = "sellerGetSalesRequest",
  GET_SALES_SUCCESS = "sellerGetSalesSuccess",
  GET_SALES_FAILURE = "sellerGetSalesFailure"
}

const sellerMutations: MutationTree<SellerState> = {
  [SellerMutations.CREATE_GOOD_REQUEST](state) {
    state.isCreateGoodLoading = true;
  },
  [SellerMutations.CREATE_GOOD_SUCCESS](state) {
    state.isCreateGoodLoading = false;
  },
  [SellerMutations.CREATE_GOOD_FAILURE](state) {
    state.isCreateGoodLoading = false;
  },
  [SellerMutations.EDIT_GOOD_REQUEST](state) {
    state.isEditGoodLoading = true;
  },
  [SellerMutations.EDIT_GOOD_SUCCESS](state) {
    state.isEditGoodLoading = false;
  },
  [SellerMutations.EDIT_GOOD_FAILURE](state) {
    state.isEditGoodLoading = false;
  },
  [SellerMutations.GET_GOOD_FOR_EDIT_REQUEST](state) {
    state.isGetGoodForEditLoading = true;
  },
  [SellerMutations.GET_GOOD_FOR_EDIT_SUCCESS](state, goodForEdit: Good) {
    state.isGetGoodForEditLoading = false;
    state.goodForEdit = goodForEdit;
  },
  [SellerMutations.GET_GOOD_FOR_EDIT_FAILURE](state) {
    state.isGetGoodForEditLoading = false;
  },
  [SellerMutations.GET_GOODS_REQUEST](state) {
    state.isGoodsListLoading = true;
  },
  [SellerMutations.GET_GOODS_SUCCESS](state, goods: NormalizedScheme<Good>) {
    state.goodsList = goods;
    state.isGoodsListLoading = false;
  },
  [SellerMutations.GET_GOODS_FAILURE](state) {
    state.isGoodsListLoading = false;
  },
  [SellerMutations.DELETE_GOOD_REQUEST](state) {
    state.isDeleteGoodLoading = true;
  },
  [SellerMutations.DELETE_GOOD_SUCCESS](state) {
    state.isDeleteGoodLoading = false;
  },
  [SellerMutations.DELETE_GOOD_FAILURE](state) {
    state.isDeleteGoodLoading = false;
  },
  [SellerMutations.GET_SALES_REQUEST](state) {
    state.isSalesListLoading = true;
  },
  [SellerMutations.GET_SALES_SUCCESS](
    state,
    sales: NormalizedScheme<Purchase>
  ) {
    state.salesList = sales;
    state.isSalesListLoading = false;
  },
  [SellerMutations.GET_SALES_FAILURE](state) {
    state.isSalesListLoading = false;
  }
};

export default sellerMutations;
