
import { defineComponent, PropType } from "vue";
import { useField } from "vee-validate";

export interface UiSelectButtonOption<T> {
  label: string;
  value: T;
}

const UiSelectButton = defineComponent({
  props: {
    name: {
      type: String,
      required: true
    },
    options: {
      type: Array as PropType<UiSelectButtonOption<unknown>[]>,
      required: true
    }
  },
  setup(props) {
    const {
      value: inputValue,
      errorMessage,
      handleBlur,
      handleChange,
      meta
    } = useField(props.name);

    return {
      handleChange,
      handleBlur,
      errorMessage,
      inputValue,
      meta
    };
  }
});

export default UiSelectButton;
